.main {
  padding: 5%;
  text-align: center;
}

.gif {
  display: flex;
}

.hide {
  display: none;
}

.images:hover + .hide {
  display: block;
  color: white;
}

.main h1 {
  font-size: 4rem;
}

.mainSkills {
  width: 80vw;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.logo {
  margin: auto;
  padding: 10%;
}

@media screen and (max-width: 1024px) {
  .main h1 {
    font-size: 3rem;
  }
}

@media screen and (max-width: 640px) {
  .main h1 {
    font-size: 2rem;
  }

  .mainSkills {
    grid-template-columns: repeat(2, 1fr);
  }
  .gif > img {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  .main {
    width: 90vw;
  }
  .mainSkills {
    width: 90vw;
  }
}
