.about {
  /* display: flex; */
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 15% 5%;
}

html {
  scroll-behavior: smooth;
}

.leftProfile {
  width: 50%;
  text-align: center;
}

.leftProfile img {
  border-radius: 50%;
  width: 80%;
  border: 1px solid white;
}

.right p {
  color: white;
  font-size: 1.4rem;
  text-align: justify;
}

.right h1 {
  font-size: 4rem;
}

.right {
  padding-top: 100px;
  margin-left: -50%;
}

.about .leftProfile {
  align-items: center;
}

@media screen and (max-width: 1024px) {
  .about {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .right h1 {
    font-size: 3rem;
  }

  .right {
    margin-left: 0;
  }
  .leftProfile img {
    width: 50vw;
  }
}

@media screen and (max-width: 640px) {
  .about {
    flex-direction: column;
    align-items: center;
  }

  .right h1 {
    font-size: 2rem;
  }
  .leftProfile img {
    width: 50vw;
  }
}

@media screen and (max-width: 480px) {
  .about {
    width: 90vw;
  }
  .leftProfile img {
    width: 40vw;
  }

  .about > div {
    padding: 10% 5%;
  }
}
