.project {
  padding: 5%;
  color: white;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.proCard {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5%;
  margin: 10% 0;
}

.cardRight h1,
p {
  color: white;
}

.projectMain {
  text-align: center;
}

.cardLeft {
  height: 100%;
  width: 30vw;
  margin: auto;
}

.cardLeft img {
  width: 30vw;
}

@media screen and (max-width: 1024px) {
  .project {
    grid-template-columns: repeat(1, 1fr);
  }
  .cardLeft {
    height: 100%;
    width: 100%;
  }
  .proCard {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .cardLeft img {
    width: 80vw;
  }
}

@media screen and (max-width: 640px) {
}

@media screen and (max-width: 480px) {
  .projectMain {
    width: 90vw;
  }
}
