    color: white;
} */

.phone h4,
.mail h4,
.footer-heading h1 {
  color: white;
}

.footer {
  width: 100%;
  padding: 6rem;
  text-align: center;
  background-color: rgba(19, 19, 19, 0.8);
}

.footer-container {
  margin: auto;
}

.left div {
  padding: 1rem;
}

.footer-heading {
  font-size: 2rem;
}

.left h4 {
  font-size: large;
}

@media screen and (max-width: 1024px) {
  .footer-heading {
    font-size: 1rem;
  }

  .left h4 {
    font-size: medium;
  }
}

@media screen and (max-width: 640px) {
  .footer-heading {
    font-size: 0.5rem;
  }
  .left h4 {
    font-size: small;
  }
}

@media screen and (max-width: 480px) {
  .footer {
    width: 90vw;
  }
}
